
.form__wrapper {
  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: repeat(2, 570px);
    column-gap: 80px;
  }
}

.form__field {
  border: none;
  padding: 0;
}

.form__field-label {
  color: var(--special-deep-color-medium);
  text-transform: uppercase;
}

.form__field-value {
  font-family: inherit;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;

  &::placeholder {
    color: var(--special-deep-color-medium);
  }
}

.form__legend {
  font-size: 24px;
  line-height: 26px;
}

.form__text {
  font-family: inherit;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;

  &::placeholder {
    color: var(--special-deep-color-medium);
  }
}

.form__list {
  margin: 0;
  padding: 0;
  list-style-type: none;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 124px;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 104px;
  }
}

.form__legend-fields {
  font-family: var(--main-font);
  font-size: 14px;
  line-height: 16px;
}

.form__button-wrapper {
  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 60px;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 568px 180px;
    column-gap: 80px;
  }
}
